
var Bacon = require("baconjs");

var traceur = require("traceur");

var _ = require("lodash");

export default function(seq) {
  try {
    var compiled = traceur.compile(seq.code,{modules:"register", generators:"parse", blockBinding:"parse"});
    return _.extend({processedCode: compiled},seq);
  } catch (exception ){
    console.log("Traceur compile error".bold.red, seq,exception, exception.trace);
    return new Bacon.Error({msg:"Traceur compile error",exception:exception});
  }
}
